"use client";

import React, { useState, useRef, useEffect } from "react";
import ReactQuill from "react-quill-new";
import "react-quill-new/dist/quill.snow.css";

export default function DynamicContent({ selected }) {
  const [content, setContent] = useState("");
  const [rowsInput, setRowsInput] = useState(3);
  const [colsInput, setColsInput] = useState(3);
  const [loading, setLoading] = useState(false);
  const [saving, setSaving] = useState(false);
  const quillRef = useRef(null);

  useEffect(() => {
    const fetchContent = async () => {
      if (!selected?.submenu) return;

      setLoading(true);
      try {
        const response = await fetch(`http://localhost:5000/api/sections/${selected.submenu.id}`);
        if (response.ok) {
          const data = await response.json();
          setContent(data.content || "");
        } else {
          setContent(""); // Reset if not found
        }
      } catch (error) {
        console.error("Error fetching content:", error);
        setContent("");
      } finally {
        setLoading(false);
      }
    };

    fetchContent();
  }, [selected]);

  const insertEditableTable = (rows, cols) => {
    if (!quillRef.current) return;
    const editor = quillRef.current.getEditor();
    if (!editor) return;

    let tableHtml = '<table class="editable-table">';
    for (let i = 0; i < rows; i++) {
      tableHtml += "<tr>";
      for (let j = 0; j < cols; j++) {
        tableHtml += "<td><br></td>";
      }
      tableHtml += "</tr>";
    }
    tableHtml += "</table><p><br></p>";

    editor.clipboard.dangerouslyPasteHTML(editor.getSelection()?.index || 0, tableHtml);
  };

  const handleSave = async () => {
    if (!selected?.submenu) return;

    setSaving(true);
    try {
      const payload = {
        title: selected.submenu.title,
        content: content,
        documentId: selected.menu.id, // Assume menu.id maps to documentId
      };

      // Try to update first (PUT)
      let response = await fetch(`http://localhost:5000/api/sections/${selected.submenu.id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(payload),
      });

      if (!response.ok) {
        // If update fails (e.g., section doesn't exist), create new (POST)
        response = await fetch('http://localhost:5000/api/sections', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(payload),
        });
      }

      if (response.ok) {
        alert("Content saved successfully!");
      } else {
        throw new Error('Failed to save content');
      }
    } catch (error) {
      console.error("Error saving content:", error);
      alert("Failed to save content. Please try again.");
    } finally {
      setSaving(false);
    }
  };

  if (!selected?.submenu) {
    return (
      <div className="p-8 text-gray-500">
        Silakan pilih menu atau submenu untuk mulai mengedit konten.
      </div>
    );
  }

  return (
  <div className="editor-wrapper flex flex-col gap-4 items-start w-full px-6">
    {/* Judul */}
    <h1 className="text-2xl font-bold text-gray-900">
      {selected.submenu.title}
    </h1>

    {/* Input Rows/Cols dan tombol */}
    <div className="flex flex-wrap items-center gap-2">
      <label className="text-sm" htmlFor="rows-input">Rows</label>
      <input
        id="rows-input"
        type="number"
        min={1}
        max={12}
        value={rowsInput}
        onChange={(e) => setRowsInput(Number(e.target.value))}
        className="w-20 border rounded p-1 text-sm"
      />

      <label className="text-sm" htmlFor="cols-input">Cols</label>
      <input
        id="cols-input"
        type="number"
        min={1}
        max={12}
        value={colsInput}
        onChange={(e) => setColsInput(Number(e.target.value))}
        className="w-20 border rounded p-1 text-sm"
      />

      <button
        onClick={() => insertEditableTable(rowsInput, colsInput)}
        className="editor-toolbar-btn editor-toolbar-btn-success"
      >
        Insert Table
      </button>

      <button
        onClick={() => {
          setContent("");
          const editor = quillRef.current?.getEditor();
          if (editor) editor.setContents([]);
        }}
        className="editor-toolbar-btn editor-toolbar-btn-secondary"
      >
        Clear
      </button>
    </div>

    {/* Editor */}
    <div className="editor-container w-full">
      <ReactQuill
        ref={quillRef}
        theme="snow"
        value={content}
        onChange={setContent}
        modules={{
          toolbar: [
            [{ header: [1, 2, false] }],
            ["bold", "italic", "underline", "strike"],
            [{ list: "ordered" }, { list: "bullet" }],
            ["link", "image"],
            [{ color: [] }, { background: [] }],
            [{ align: [] }],
            ["clean"],
          ],
        }}
      />
    </div>

    {/* Tombol Simpan */}
    <div className="mt-4 flex gap-3">
      <button
        onClick={handleSave}
        disabled={saving}
        className="editor-toolbar-btn editor-toolbar-btn-primary"
      >
        {saving ? "Menyimpan..." : "Simpan"}
      </button>
    </div>
  </div>
);
}
