"use client";

import { useState } from "react";
import Sidebar from "./components/Sidebar";
import DynamicContent from "./components/DynamicContent";
import AddMenuModal from "./components/AddMenuModal";
import ConfirmModal from "./components/ConfirmModal"; // üî• modal baru
import "./styles/dashboard.css";
import "react-quill-new/dist/quill.snow.css";

export default function DashboardPage() {
  const [selected, setSelected] = useState({ menu: null, submenu: null });
  const [showAddModal, setShowAddModal] = useState(false);
  const [showConfirmModal, setShowConfirmModal] = useState(false);
  const [targetDelete, setTargetDelete] = useState(null); // üî• simpan target yang mau dihapus

  const [menuData, setMenuData] = useState([
    {
      id: 1,
      title: "Employee Management",
      submenus: [
        { id: 11, title: "Employee List" },
        { id: 12, title: "Create New Employee" },
      ],
    },
    {
      id: 2,
      title: "Claim & Travel",
      submenus: [
        { id: 21, title: "Travel Policy" },
        { id: 22, title: "Travel Form" },
      ],
    },
  ]);

  const handleAddMenu = (newMenu) => {
    setMenuData((prev) => [
      ...prev,
      { id: Date.now(), title: newMenu.title, submenus: [] },
    ]);
  };

  const handleDeleteMenu = (menuId) => {
    setMenuData((prev) => prev.filter((m) => m.id !== menuId));
    if (selected?.menu?.id === menuId) setSelected({ menu: null, submenu: null });
  };

  const handleDeleteSubmenu = (menuId, subId) => {
    setMenuData((prev) =>
      prev.map((m) =>
        m.id === menuId
          ? { ...m, submenus: m.submenus.filter((s) => s.id !== subId) }
          : m
      )
    );
    if (selected?.submenu?.id === subId) setSelected({ menu: null, submenu: null });
  };

  const confirmDelete = (target) => {
    setTargetDelete(target);
    setShowConfirmModal(true);
  };

  const executeDelete = () => {
    if (!targetDelete) return;
    if (targetDelete.type === "menu") handleDeleteMenu(targetDelete.id);
    else handleDeleteSubmenu(targetDelete.menuId, targetDelete.id);
    setShowConfirmModal(false);
  };

  return (
    <div className="dashboard-layout">
      <Sidebar
        menuData={menuData}
        selected={selected}
        setSelected={setSelected}
        onAddMenu={() => setShowAddModal(true)}
        onDelete={confirmDelete}
      />

      <main className="dashboard-main">
  <header className="dashboard-header mb-6">
    <h1 className="text-2xl font-bold text-gray-800">Dashboard Admin</h1>
    <p className="text-gray-600">
      Kelola kategori dan konten dokumentasi HRMS di sini
    </p>
  </header>

  <div className="w-full">
    <DynamicContent selected={selected} />
  </div>

  {showAddModal && (
    <AddMenuModal
      onClose={() => setShowAddModal(false)}
      onSave={handleAddMenu}
    />
  )}

  {showConfirmModal && (
    <ConfirmModal
      onClose={() => setShowConfirmModal(false)}
      onConfirm={executeDelete}
      title="Hapus Item"
      message="Apakah kamu yakin ingin menghapus item ini?"
    />
  )}
</main>

    </div>
  );
}
