"use client";

import { useState } from "react";
import {
  ChevronDown,
  ChevronRight,
  Plus,
  Trash2,
  X,
  Pencil,
  Check,
} from "lucide-react";
import SidebarItem from "./SidebarItem";

export default function Sidebar({
  menuData,
  selected,
  setSelected,
  onAddMenu,
  onDelete,
  onRename, // ‚úÖ Tambahin handler rename biar bisa update ke backend
}) {
  const [activeMenu, setActiveMenu] = useState(null);
  const [editingItem, setEditingItem] = useState(null); // { type: "menu" | "submenu", id }
  const [newTitle, setNewTitle] = useState("");

  const toggleMenu = (menuId) => {
    setActiveMenu(activeMenu === menuId ? null : menuId);
  };

  const handleRenameStart = (type, item) => {
    setEditingItem({ type, id: item.id });
    setNewTitle(item.title);
  };

  const handleRenameSubmit = async () => {
    if (newTitle.trim() === "") return;
    if (onRename) {
      await onRename(editingItem, newTitle);
    }
    setEditingItem(null);
    setNewTitle("");
  };

  return (
    <aside className="dashboard-sidebar">
      {/* ==== Header ==== */}
      <div className="dashboard-sidebar-header">
        <img src="/duluin-hrms.png" alt="Logo HRMS" className="dashboard-logo" />
        <h3 className="dashboard-doc-title">Documentation</h3>

        <div className="dashboard-header-btns">
          <button onClick={onAddMenu} className="dashboard-add-btn">
            <Plus size={16} />
            <span>Add</span>
          </button>
        </div>
      </div>

      {/* ==== Menu Navigasi ==== */}
      <nav className="dashboard-nav">
        {menuData.map((menu) => (
          <div key={menu.id}>
            <div className="flex justify-between items-center group">
              <button
                onClick={() => toggleMenu(menu.id)}
                className={`dashboard-menu-btn ${
                  activeMenu === menu.id ? "active" : ""
                }`}
              >
                {/* === Inline rename menu === */}
                {editingItem?.type === "menu" && editingItem.id === menu.id ? (
                  <input
                    value={newTitle}
                    onChange={(e) => setNewTitle(e.target.value)}
                    onBlur={handleRenameSubmit}
                    onKeyDown={(e) => e.key === "Enter" && handleRenameSubmit()}
                    className="border border-gray-300 rounded px-2 py-1 text-sm w-full"
                    autoFocus
                  />
                ) : (
                  <span>{menu.title}</span>
                )}

                {activeMenu === menu.id ? (
                  <ChevronDown size={16} />
                ) : (
                  <ChevronRight size={16} />
                )}
              </button>

              <div className="flex items-center gap-1 opacity-0 group-hover:opacity-100 transition">
                {/* ‚úèÔ∏è Rename Menu */}
                <button
                  onClick={() => handleRenameStart("menu", menu)}
                  className="text-gray-400 hover:text-blue-500"
                >
                  <Pencil size={15} />
                </button>

                {/* üóëÔ∏è Delete Menu */}
                <button
                  onClick={() => onDelete({ type: "menu", id: menu.id })}
                  className="text-gray-400 hover:text-red-500"
                >
                  <Trash2 size={15} />
                </button>
              </div>
            </div>

            {/* === Submenu === */}
            {activeMenu === menu.id && (
              <div className="dashboard-submenu">
                {menu.submenus.map((sub) => (
                  <div
                    key={sub.id}
                    className="flex justify-between items-center group pl-3"
                  >
                    {editingItem?.type === "submenu" &&
                    editingItem.id === sub.id ? (
                      <input
                        value={newTitle}
                        onChange={(e) => setNewTitle(e.target.value)}
                        onBlur={handleRenameSubmit}
                        onKeyDown={(e) =>
                          e.key === "Enter" && handleRenameSubmit()
                        }
                        className="border border-gray-300 rounded px-2 py-1 text-sm w-full"
                        autoFocus
                      />
                    ) : (
                      <SidebarItem
                        submenu={sub}
                        isSelected={selected?.submenu?.id === sub.id}
                        onClick={() => setSelected({ menu, submenu: sub })}
                      />
                    )}

                    <div className="flex items-center gap-1 opacity-0 group-hover:opacity-100 transition">
                      {/* ‚úèÔ∏è Rename Submenu */}
                      <button
                        onClick={() => handleRenameStart("submenu", sub)}
                        className="text-gray-400 hover:text-blue-500"
                      >
                        <Pencil size={13} />
                      </button>

                      {/* ‚ùå Delete Submenu */}
                      <button
                        onClick={() =>
                          onDelete({
                            type: "submenu",
                            menuId: menu.id,
                            id: sub.id,
                          })
                        }
                        className="text-gray-400 hover:text-red-500"
                      >
                        <X size={13} />
                      </button>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        ))}
      </nav>

      {/* ==== Logout ==== */}
      <div className="dashboard-logout">
        <button>Logout</button>
      </div>
    </aside>
  );
}
